.contact6-container {
  height: 177px;
  display: flex;
  position: relative;
  align-items: flex-start;
  padding-top: 0px;
  flex-direction: column;
  padding-bottom: 0px;
}
.contact6-max-width {
  height: 177px;
  align-self: center;
}
.contact6-content1 {
  width: 100%;
  height: 205px;
  align-items: center;
  margin-bottom: var(--dl-space-space-threeunits);
  justify-content: space-between;
}
.contact6-content2 {
  gap: var(--dl-space-space-unit);
  display: flex;
  align-self: stretch;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.contact6-content3 {
  gap: var(--dl-space-space-oneandhalfunits);
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.contact6-content4 {
  gap: var(--dl-space-space-unit);
  display: flex;
  align-self: stretch;
  align-items: center;
  flex-direction: row;
  justify-content: flex-start;
}
.contact6-contact-info1 {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.contact6-text3 {
  align-self: flex-start;
  text-align: center;
}
.contact6-content5 {
  gap: var(--dl-space-space-unit);
  display: flex;
  align-self: stretch;
  align-items: center;
  flex-direction: row;
  justify-content: flex-start;
}
.contact6-contact-info2 {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.contact6-text4 {
  align-self: flex-start;
  text-align: center;
}
.contact6-text6 {
  display: inline-block;
}
.contact6-text7 {
  display: inline-block;
}
.contact6-text8 {
  display: inline-block;
}
.contact6-text9 {
  display: inline-block;
}
@media(max-width: 991px) {
  .contact6-content1 {
    align-items: flex-start;
    justify-content: flex-start;
  }
}
@media(max-width: 767px) {
  .contact6-content1 {
    gap: var(--dl-space-space-oneandhalfunits);
    flex-direction: column;
  }
}
